:root {
  --color: #3c3163;
  --transition-time: 0.5s;
}

* {
  box-sizing: border-box;
}

.card {
  font-family: 'Heebo';
  /* --bg-filter-opacity: 0.5; */
  background-image: linear-gradient(rgba(0,0,0,var(--bg-filter-opacity)),rgba(0,0,0,var(--bg-filter-opacity))), var(--bg-img);
  height: 15em;
  width: 10em;
  font-size: 1.5em;
  color: white;
  border-radius: 1em;
  padding: 1em;
  /*margin: 2em;*/
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  background-size: cover;
  background-position: center;
  box-shadow: 0 0 5em -1em black;
  transition: all, 0.5s;
  transition: all, var(--transition-time);
  position: relative;
  overflow: hidden;
  border: 10px solid #ccc;
  text-decoration: none;
}

.card:hover {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}

.card h1 {
  margin: 0;
  font-size: 1.2em;
  line-height: 1.0em;
}

.card p {
  font-size: 0.65em;
  font-family: 'Open Sans';
  margin-top: 0.5em;
  line-height: 2em;
}

.card .tags {
  display: -webkit-flex;
  display: flex;
}

.card .tags .tag {
  font-size: 0.5em;
  background: rgba(255,255,255,0.5);
  border-radius: 0.3rem;
  padding: 0.5em;
  margin-right: 0.5em;
  line-height: 1.5em;
  transition: all, 0.5s;
  transition: all, var(--transition-time);
  font-weight: bolder;
  cursor: pointer;
  /* background-color: #36454F; */
}

.card:hover .tags .tag {
  background: #3c3163;
  background: var(--color);
  color: white;
}

.link {
  color: #fff;
}

.card:before, .card:after {
  content: '';
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: top left;
          transform-origin: top left;
  border-radius: 50%;
  position: absolute;
  left: -50%;
  top: -50%;
  z-index: -5;
  transition: all, 0.5s;
  transition: all, var(--transition-time);
  transition-timing-function: ease-in-out;
}

.card:before {
  background: #ddd;
  width: 250%;
  height: 250%;
}

.card:after {
  background: white;
  width: 200%;
  height: 200%;
}

.card:hover {
  color: #3c3163;
  color: var(--color);
}

.card:hover:before, .card:hover:after {
  -webkit-transform: scale(1);
          transform: scale(1);
}

@media(max-width:786px) {
  .card {
    margin: 0.5rem auto;
    -webkit-align-items:center;
            align-items:center;
    -webkit-justify-content: center;
            justify-content: center;
  }

  

  .card > div{
    margin: auto;
  }
}

